
#showDialog
	display: inline
	cursor: pointer
	line-height: 1.175rem
	padding: 0rem 0.15rem
	background-color: #ffffff00
	border: 1px dotted #4b0082
	font-size: 1rem
	color: $mainColor

	&::before
		content: "\01F449"
		font-family: $fontEmoji
		font-size: 1.1rem
		line-height: 0.75
		margin-right: 0.2rem

	&::after
		content: "\01F448"
		font-family: $fontEmoji
		font-size: 1.1rem
		line-height: 0.75
		margin-left: 0.2rem

.greeting
	margin: auto auto
	border: 0.35rem double #4b0082
	border-radius: 1rem
	background-color: #ffffff

	&::backdrop
		background-color: rgba(0,0,0,0.85)

	#inputName
		padding: 2.5rem 0.5rem 1rem 0.5rem

	label
		display: block
		padding-bottom: 0.1rem
		font-family: $fontAlice
		font-size: 1.5rem
		color: $mainColor

#insertName
	background-color: #dcdcdc
	font-size: 1.15rem
	font-family: $fontAlice
	padding: 0.05rem .35rem
	border-color: #191970
	border-style: dotted
	border-radius: .65rem
	caret-color: #4b0082
	margin: 0.5rem 0 1rem
	box-sizing: border-box

	&:focus
		background-color: #f5f5f5
		outline: none
	
#confirmBtn
	display: inline-block
	outline: none
	cursor: pointer
	font-size: 1.2rem
	font-weight: bolder
	padding: .1rem .4rem
	margin-left: 0.5rem
	border: 1px solid #000000
	border-radius: .75rem
	background-color: #ffffff
	box-shadow: inset 0 0 3px 1px #000000
	
	&:hover, &:focus
		color: rgb(0,100,0)
		box-shadow: inset 0 0 3px 1px rgb(0,100,0)
		border: 1px solid rgb(0,100,0)

.cancel
	display: block
	cursor: pointer
	position: absolute
	width: 2rem
	height: 2rem
	top: 0.125rem
	right: 0.125rem
	background: none
	border: 3px solid #ff000000
	border-radius: 0.5rem
	
	&:hover, &:focus
			border: 3px solid red
	
	span
		font-family: $fontEmoji
		font-size: 1rem
		line-height: 0.75

.error
	display: block
	position: absolute
	top: 0.5rem
	left: 1px
	overflow: visible
	width: 13.3rem
	padding: 0
	font-family: $fonthtml
	font-size: 95%
	color: #ffffff
	background-color: rgba(0,0,0,0.75)
	border-radius: 0.3rem
	box-sizing: border-box
	
.error.active
	height: 4rem
	padding: 0.5rem

.error.active.color
	background-color: rgba(128,0,0,0.75)
	

